.contanier{
    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.contanier .game-board{
    display: flex;
    flex-wrap: wrap;
    border: 5px solid black;
    width: 300px;
    height: 300px;
}
.squre{
    position: relative;
    box-sizing: border-box;
    width: 100px; 
       display: flex;
    align-items: center;
    justify-content: center;
    font-size: 80px;
    border: 1px solid black;
    height : 100px;
}
.X {
    color: red;
}
.O{
    color: green;
}
